@import "headings";

@mixin copy {

  @include heading-sizes;

  b, strong {
    font-weight: bold; }

  em, i {
    font-style: italic; }

  p, li, ul, ol, pre, blockquote, .code-block {
    &:not(:last-child) {
      margin-bottom: 1em; } }

  h1, h2, h3, h4, h5, h6 {
    font-weight: bold;

    &:not(:first-child) {
      margin-top: 1em; }

    h1 + &, h2 + &, h3 + &, h4 + &, h5 + &, h6 + & {
      margin-top: 0; }

    &:not(:last-child) {
      margin-bottom: 0.3em; } }

  ul, ol {
    padding-left: 2em; }

  ul li {
    list-style-type: disc; }

  *:not(pre) > code {
    border-radius: 0.3em;
    padding-left:  0.2em;
    padding-right: 0.2em; }

  pre {
    font-size:     0.9em;
    overflow:      auto;
    white-space:   pre;
    border-radius: 0.3em;

    > code {
      padding: 1rem !important; } }

  blockquote, aside {
    padding-left:  3em;
    padding-right: 3em;
    font-style:    italic;
    opacity:       0.9;

    > * {
      position:      relative;

      &:first-child:before {
        position:    absolute;
        top:         -0.2em;
        left:        0;
        margin-left: -50px;
        font-size:   4em;
        font-style:  normal;
        opacity:     0.5; } } }

  blockquote > *:first-child:before {
    content: ''; }

  aside > *:first-child:before, .exclamation + blockquote > *:first-child:before {
    content: '!'; } }
