@at-root {

  @import "node_modules/breakpoint-slicer/stylesheets/breakpoint-slicer";
  @import "../../components/horizontal-menu/mixins";



  ////------------------------------------------
  .route-localeIndex {
    //------------------------------------------

    // ----- Layout -----
    position: relative;

    // ----- Content layout -----

    // ----- RWD -----
    @include to(xs) {
      margin-bottom: 1rem;

      .route-localeIndex-horizontalMenu {
        @include horizontal-menu-vertical;
        padding-top: 1rem; } }

    @include from(s) {
      .route-localeIndex-horizontalMenu {
        @include horizontal-menu-horizontal;
        padding-top: 2rem; } } }


  ////------------------------------------------
  .route-localeIndex-cards {
    //------------------------------------------

    // ----- Layout -----
    overflow: hidden; }



  ////------------------------------------------
  .route-localeIndex-cards-inner {
    //------------------------------------------

    // ----- Layout -----
    $padding:       5%;
    padding-top:    $padding;
    padding-bottom: $padding;

    $margin:        4rem;
    margin-right:   -$margin;
    margin-bottom:  -$margin;

    max-width: 1200px !important;

    // ----- Layout -----
    display:   flex;
    flex-wrap: wrap;

    .route-localeIndex-card {
      flex-basis:  400px;
      flex-grow:   1;
      //flex-shrink: 0

      margin-right:  $margin;
      margin-bottom: $margin; } }



  ////------------------------------------------
  .route-localeIndex-footer {
    //------------------------------------------

    // ----- Layout -----
    $padding:       1rem;
    padding-top:    $padding;
    padding-bottom: $padding; } }
